___CSS_LOADER_EXPORT___.push([module.id, ".ownTime[data-v-2430f48e]{margin-right:45px}.otherTime[data-v-2430f48e]{margin-left:45px}.bubbleWrapper[data-v-2430f48e]{align-self:flex-end;color:#fff;display:flex;flex-direction:column;justify-content:flex-end;padding:10px}.inlineContainer[data-v-2430f48e]{display:inline-flex}.inlineContainer.own[data-v-2430f48e]{flex-direction:row-reverse}.inlineIcon[data-v-2430f48e]{-o-object-fit:contain;object-fit:contain;width:40px}.ownBubble[data-v-2430f48e]{background-color:#242d42;border:1px solid #242d42;border-radius:16px 16px 0 16px}.noteBubble[data-v-2430f48e],.ownBubble[data-v-2430f48e]{line-height:25px;margin:6px 8px;max-width:700px;min-width:60px;padding:14px 18px}.noteBubble[data-v-2430f48e]{border-radius:16px 16px 0 16px}.referredBubble[data-v-2430f48e]{border-radius:16px 16px 0 16px}.otherBubble[data-v-2430f48e],.referredBubble[data-v-2430f48e]{line-height:25px;margin:6px 8px;max-width:700px;min-width:60px;padding:14px 18px}.otherBubble[data-v-2430f48e]{background-color:#6c8ea4;border:1px solid #54788e;border-radius:16px 16px 16px 0}.own[data-v-2430f48e]{align-self:flex-start}.other[data-v-2430f48e]{align-self:flex-end}span.other[data-v-2430f48e],span.own[data-v-2430f48e]{color:grey;font-size:14px}", "",{"version":3,"sources":["webpack://./modules/ticket/templates/js/fields/Detail/MessagesField.vue"],"names":[],"mappings":"AA2MA,0BACI,iBACJ,CAEA,4BACI,gBACJ,CAEA,gCAKI,mBAAoB,CACtB,UAAW,CAJT,YAAa,CAEb,qBAAsB,CADtB,wBAAyB,CAFzB,YAMJ,CACA,kCACE,mBACF,CACA,sCACE,0BACF,CACA,6BAEE,qBAAmB,CAAnB,kBAAmB,CADnB,UAEF,CACA,4BAMI,wBAAyB,CAEzB,wBAAyB,CADzB,8BAEJ,CAEA,yDANI,gBAAiB,CADjB,cAAe,CAFf,eAAgB,CADhB,cAAe,CAEf,iBAeJ,CAPA,6BAMI,8BACJ,CAGA,iCAMI,8BACJ,CAEA,+DAJI,gBAAiB,CADjB,cAAe,CAFf,eAAgB,CADhB,cAAe,CAEf,iBAgBJ,CAVA,8BAMI,wBAAyB,CAEzB,wBAAyB,CADzB,8BAGJ,CACA,sBACI,qBACJ,CACA,wBACI,mBACJ,CACA,sDAGE,UAAW,CADX,cAEF","sourcesContent":["<template>\n    <div id=\"chat-messages\" class=\"p-6 rounded-sm w-full relative\" >\n\n        <div class=\"bubbleWrapper\" v-if=\"messages.length\" v-for=\"message in messages\">\n            <div class=\"inlineContainer\" :class=\"typeClass(message)\">\n                <div :class=\"bubbleClass(message)\" v-html=\"message.text\" />\n                <img class=\"inlineIcon\" :src=\"message.logo\">\n            </div>\n            <span :class=\"timeClass(message)\">\n                <div class=\"my-1\" v-if=\"message.modelable_type != 'user' && message.message_type == 'message'\">\n                    {{ __(\"Message From\") }}: {{ message.admin }}\n                </div>\n\n                <div class=\"my-1\" v-if=\"message.message_type != 'message'\">\n                    <div class=\"flex flex-wrap\" v-if=\"message.message_type == 'referred'\">\n                        <div>\n                            {{ __(\"Referred\") }}: {{ message.referredTo }}\n                        </div>\n                        <div class=\"ml-4 pl-4 border-l-2\">\n                          {{ __(\"Referrer\") }}: {{ message.referredFrom }}\n                        </div>\n                    </div>\n                    <div v-else>\n                        {{ __(\"Note\") }} - {{ __(\"Writer\") }}: {{ message.admin }}\n                    </div>\n                </div>\n                <div \n                    class=\"w-full\"\n                    :class=\"{\n                        'justify-end flex': message.modelable_type == 'user'\n                    }\"\n                >\n                    {{ message.created_at }}\n                </div>\n\n                <div class=\"w-full flex-wrap gap-y-1.5 flex mt-2\">\n                    <a\n                        :href=\"file.path\"\n                        target=\"_blank\"\n                        v-for=\"(file, index) in message.files\"\n                        :key=\"index\"\n                        :class=\"{\n                            'flex-row-reverse': message.modelable_type == 'user'\n                        }\"\n                        class=\"flex w-full justify-start gap-2 text-dynamic-secondary\"\n                    >\n                        <img :src=\"file.path\" class=\"w-8 h-8 rounded-lg\">\n                        <p class=\"text-xs line-clamp-1 items-center flex\">{{ getFileName(file.path) }}</p>\n                    </a>\n                </div>\n            </span>\n        </div>\n\n    </div>\n\n    <div v-if=\"canSendMessage\" class=\"bg-gray-100 border-2 grid grid-cols-1 md:gap-x-5 md:grid-cols-5 md:px-[50px] my-2 rounded-lg shadow-lg w-full\">\n        \n        <Reply\n            :resourceId=\"resourceId\"\n            class=\"md:border-r-2 md:pr-4 col-span-2\"\n        />\n        <NoteOrReferred\n            :class=\"{\n                'md:border-r-2 md:pr-4': field.canClose || field.canReferral\n            }\"\n            class=\"col-span-2\"\n            :resourceId=\"resourceId\"\n        />\n\n        <div class=\"flex\" v-if=\"field.canClose || field.canReferral\">\n            <button\n                v-if=\"field.canReferral\"\n                @click.stop=\"showAction('referred-to-action')\"\n                type=\"button\"\n                class=\"bg-blue-400 border border-gray-200 dark:border-gray-600 font-medium h-14 hover:bg-gray-100 hover:text-blue-700 mx-auto my-auto px-5 py-2.5 rounded-lg text-white text-sm\">\n                {{ __('Ticket referral') }}\n            </button>\n            <button\n                v-if=\"field.canClose\"\n                @click.stop=\"showAction('close-ticket-action')\"\n                type=\"button\"\n                class=\"bg-blue-400 border border-gray-200 dark:border-gray-600 font-medium h-14 hover:bg-gray-100 hover:text-blue-700 mx-auto my-auto px-5 py-2.5 rounded-lg text-white text-sm\">\n                {{ __('Close Ticket') }}\n            </button>\n\n        </div>\n\n    </div>\n\n\n</template>\n\n<script>\n\nimport Reply from \"../../components/Reply\"\nimport NoteOrReferred from \"../../components/NoteOrReferred\"\n\nexport default {\n\n  data : () => ({\n    loading : true,\n    messages: []\n  }),\n\n  props: {\n    field: {\n      type: Object,\n      required: true,\n    },\n    fieldName: {\n      type: String,\n      default: '',\n    },\n  },\n\n  components :{\n    Reply, NoteOrReferred\n  },\n\n  methods:{\n\n    getFileName(url) {\n        const index = url.indexOf(\"message\");\n        return url.substring(index + 8);\n    },\n\n    showAction(key) {\n        Nova.$emit('open-action-model', key)\n    },\n\n    typeClass(record){\n        return record.modelable_type == 'user' ? 'other' : 'own'\n    },\n\n    bubbleClass(record){\n        if(record.modelable_type == 'user') {\n            return 'otherBubble other';\n        }\n        if(record.message_type == 'message') {\n            return 'ownBubble own';\n        }        \n        if(record.message_type == 'note') {\n            return 'bg-red-400 border-1 border-red-400 noteBubble own';\n        }\n        return 'bg-yellow-300 border-1 border-yellow-300 text-gray-600 referredBubble own';\n    },\n\n    timeClass(record){\n        return record.modelable_type == 'user' ? 'other otherTime' : 'own ownTime'\n    },\n\n  },\n\n  mounted(){\n    this.messages = this.field.options\n\n    Nova.$on('update-ticket-messages', (messages) => {\n        this.messages = messages\n    })\n    // let chatWrapper = document.querySelector('#chat-messages');\n    // chatWrapper.scrollTo(0, chatWrapper.offsetHeight );\n\n  },\n\n  beforeUnmount() {\n    Nova.$off('update-ticket-messages')\n  },\n\n  computed: {\n\n    resourceId(){\n      return Number(this.$attrs[\"resource-id\"])\n    },\n\n    label() {\n      return this.fieldName || this.field.name\n    },\n\n    canSendMessage(){\n      return this.field.canSendMessage\n    },\n\n    fieldValue() {\n      if (\n        this.field.value === '' ||\n        this.field.value === null ||\n        this.field.value === undefined\n      ) {\n        return false\n      }\n\n      return String(this.field.value)\n    },\n\n    shouldDisplayAsHtml() {\n      return this.field.asHtml\n    },\n  },\n};\n</script>\n<style scoped>\n    \n\n.ownTime{\n    margin-right: 45px;\n}\n\n.otherTime{\n    margin-left: 45px;\n}\n\n.bubbleWrapper {\n    padding: 10px 10px;\n    display: flex;\n    justify-content: flex-end;\n    flex-direction: column;\n    align-self: flex-end;\n  color: #fff;\n}\n.inlineContainer {\n  display: inline-flex;\n}\n.inlineContainer.own {\n  flex-direction: row-reverse;\n}\n.inlineIcon {\n  width:40px;\n  object-fit: contain;\n}\n.ownBubble {\n    min-width: 60px;\n    max-width: 700px;\n    padding: 14px 18px;\n    margin: 6px 8px;\n    line-height: 25px;\n    background-color: #242D42;\n    border-radius: 16px 16px 0 16px;\n    border: 1px solid #242D42;\n}\n\n.noteBubble {\n    min-width: 60px;\n    max-width: 700px;\n    padding: 14px 18px;\n    margin: 6px 8px;\n    line-height: 25px;\n    border-radius: 16px 16px 0 16px;\n}\n\n\n.referredBubble {\n    min-width: 60px;\n    max-width: 700px;\n    padding: 14px 18px;\n    margin: 6px 8px;\n    line-height: 25px;\n    border-radius: 16px 16px 0 16px; \n}\n\n.otherBubble {\n    min-width: 60px;\n    max-width: 700px;\n    padding: 14px 18px;\n    margin: 6px 8px;\n    line-height: 25px;\n    background-color: #6C8EA4;\n    border-radius: 16px 16px 16px 0;\n    border: 1px solid #54788e;\n  \n}\n.own {\n    align-self: flex-start;\n}\n.other {\n    align-self: flex-end;\n}\nspan.own,\nspan.other{\n  font-size: 14px;\n  color: grey;\n}\n\n</style>"],"sourceRoot":""}]);
